const ButtonTheme = createMuiTheme({
  palette: {
    primary: green,
  },
});

const createData = ({ id, category, description, action }: Data) => {
  return { id, category, description, action };
};

const headCells: HeadCell[] = [
  { id: "id", numeric: true, label: "ID" },
  { id: "category", numeric: false, label: "Type" },
  { id: "description", numeric: false, label: "Description" },
  { id: "action", numeric: false, label: "Commands" },
];

function EnhancedTableHead(props: EnhancedTableProps) {
  const { order, orderBy, onRequestSort } = props;

  const classes = useStyles();

  const createSortHandler = (property: keyof Data) => (
    event: React.MouseEvent<unknown>
  ) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead className={classes.tableHead}>
      <TableRow>
        {headCells.map((headCell: HeadCell, index: number) => {
          return (
            <StyledTableCell
              key={headCell.id}
              align={headCell.numeric ? "right" : "left"}
              sortDirection={orderBy === headCell.id ? order : false}
            >
              <TableSortLabel
                className={
                  (orderBy === headCell.id ? classes.sortLabelActive : "",
                  classes.sortLabelRoot)
                }
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : "asc"}
                onClick={createSortHandler(headCell.id)}
              >
                {headCell.label}
              </TableSortLabel>
            </StyledTableCell>
          );
        })}
        <StyledTableCell key={"actions"} align="center">
          <StyledColumnLabel>Actions</StyledColumnLabel>
        </StyledTableCell>
      </TableRow>
    </TableHead>
  );
}

const EnhancedTable = () => {
  const { rerender, openModal, actionsList } = useSelector(actionsSelector);
  const { accessLevel } = useSelector(adminSelector);
  const dispatch = useDispatch();

  const [order, setOrder] = useState<Order>("desc");
  const [orderBy, setOrderBy] = useState<keyof Data>("id");
  const [page, setPage] = useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = useState<number>(25);
  const [currID, setCurrID] = useState("");
  const [currType, setCurrType] = useState<actionTypes>("Compress");
  const [currCommands, setCurrCommands] = useState<string>("");
  const [currDescription, setCurrDescription] = useState<string>("");
  const [openDeleted, setOpenDeleted] = useState(false);
  const [openSuccess, setOpenSuccess] = useState(false);
  const [showDelete, setShowDelete] = useState(false);

  const [searchText, setSearchText] = useState<string>("");

  const [searchResult, setSearchResult] = useState<Data[]>([]);

  const classes = useStyles();

  useEffect(() => {
    const api = GET_ACTIONS;
    fetch(api, {
      method: "POST",
    })
      .then((response) => {
        return response.json();
      })
      .then((result) => {
        const data = result.map((el: any) => {
          el.id = parseInt(el.id);
          return createData(el);
        });
        dispatch(updateList(data));
        setSearchResult(data);
        // dispatch(updateSearchList(data));
      });
  }, [dispatch, rerender]);

  const handleRequestSort = (event: any, property: keyof Data) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const handleChangePage = (event: any, newPage: any) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: any) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleOpenModal = () => {
    dispatch(openModalAction());
  };

  const handleShowDelete = () => {
    setShowDelete(!showDelete);
  };

  const handleConfirmDelete = () => {
    const data = {
      id: currID,
    };
    const api = DELETE_ACTION;
    fetch(api, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
      },
      body: qs.stringify(data),
    });
    dispatch(deleteAction(parseInt(currID)));
    handleResetSelected();
    setOpenDeleted(!openDeleted);
    setShowDelete(!showDelete);
  };

  const handleSetCurrAction = (
    type: actionTypes,
    description: string,
    commands: string,
    id: string,
    action: string
  ) => () => {
    setCurrID(id);
    setCurrType(type);
    setCurrDescription(description);
    setCurrCommands(commands);
    if (action === "edit") dispatch(openModalAction());
    else setShowDelete(!showDelete);
  };

  const handleResetSelected = () => {
    setCurrType("Compress");
    setCurrDescription("");
    setCurrCommands("");
    setCurrID("");
  };

  const handleCloseDeleted = (
    event?: React.SyntheticEvent,
    reason?: string
  ) => {
    if (reason === "clickaway") return;
    setOpenDeleted(false);
  };

  const handleSuccess = () => {
    setOpenSuccess(true);
  };

  const handleCloseSuccess = (
    event?: React.SyntheticEvent,
    reason?: string
  ) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenSuccess(false);
  };

  const hasAccess = () => {
    return accessLevel === 0;
  };

  const handleSearch = (event: any) => {
    const value = event.target.value.toLowerCase();
    setSearchText(value);
    if (!value || value.length === 0) {
      setSearchResult(actionsList);
      return;
    }
    let result = actionsList.filter(
      (elm: any) =>
        elm.id.toString().includes(value) ||
        value.includes(elm.category.toLowerCase()) ||
        elm.category.toLowerCase().includes(value) ||
        elm.action.toLowerCase().includes(value) ||
        elm.description.toLowerCase().includes(value) ||
        value.includes(elm.description.toLowerCase())
    );
    setPage(0);
    setSearchResult(result);
  };

  return (
    <div>
      <Paper>
        <div className={classes.flexDiv}>
          <div className={classes.flexDiv2}>
            <TextField
              id="filter"
              autoComplete="off"
              autoCorrect="off"
              spellCheck="false"
              variant="outlined"
              value={searchText}
              onChange={handleSearch}
              inputProps={{
                style: {
                  margin: "auto",
                  width: "35ch",
                  padding: "6px 6px 7px ",
                },
              }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Typography>Filter: </Typography>
                  </InputAdornment>
                ),
              }}
            />
          </div>
          <aside>
            <Tooltip title={hasAccess() ? "" : "Admin only"}>
              <Button
                variant="outlined"
                // color="primary"
                color={hasAccess() ? "primary" : "default"}
                onClick={hasAccess() ? handleOpenModal : () => {}}
                startIcon={<AddIcon />}
                disableRipple
              >
                Add Action
              </Button>
            </Tooltip>
          </aside>
        </div>
        <ThemeProvider theme={ButtonTheme}>
          <TableContainer style={{ width: "100%", overflow: "hidden" }}>
            <Table
              aria-labelledby="tableTitle"
              size="small"
              aria-label="enhanced table"
            >
              <colgroup>
                <col style={{ width: "5%" }} />
                <col style={{ width: "10%" }} />
                <col style={{ width: "12%" }} />
                <col style={{ width: "63%" }} />
                <col style={{ width: "10%" }} />
              </colgroup>
              <EnhancedTableHead
                order={order}
                orderBy={orderBy}
                onRequestSort={handleRequestSort}
              />
              <TableBody>
                {searchResult.length === 0 ? (
                  <TableRow hover>
                    <TableCell colSpan={6}>No matching records found</TableCell>
                  </TableRow>
                ) : (
                  stableSort(searchResult, getComparator(order, orderBy))
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((row: any, index: number) => {
                      return (
                        <StyledTableRow
                          tabIndex={-1}
                          key={row.id}
                          className={classes.alignTop}
                          hover
                        >
                          <StyledTableCell
                            component="th"
                            align="right"
                            scope="row"
                          >
                            {row.id}
                          </StyledTableCell>
                          <StyledTableCell align="left">
                            {row.category}
                          </StyledTableCell>
                          <StyledTableCell align="left">
                            {row.description}
                          </StyledTableCell>
                          <StyledTableCell align="left">
                            {row.action}
                          </StyledTableCell>
                          <StyledTableCell align="left">
                            <div className={classes.flexDiv2}>
                              <Tooltip
                                title={hasAccess() ? "Edit" : "Admin only"}
                              >
                                <IconButton
                                  disableRipple
                                  aria-label="edit"
                                  // color="primary"
                                  color={hasAccess() ? "primary" : "default"}
                                  size="small"
                                  onClick={
                                    hasAccess()
                                      ? handleSetCurrAction(
                                          row.category,
                                          row.description,
                                          row.action,
                                          row.id,
                                          "edit"
                                        )
                                      : () => {}
                                  }
                                >
                                  <EditIcon />
                                </IconButton>
                              </Tooltip>
                              <Tooltip
                                title={hasAccess() ? "Delete" : "Admin only"}
                              >
                                <IconButton
                                  component="div"
                                  aria-label="delete"
                                  color={hasAccess() ? "secondary" : "default"}
                                  size="small"
                                  disableRipple
                                  onClick={
                                    hasAccess()
                                      ? handleSetCurrAction(
                                          row.category,
                                          row.description,
                                          row.action,
                                          row.id,
                                          "delete"
                                        )
                                      : () => {}
                                  }
                                >
                                  <DeleteIcon />
                                </IconButton>
                              </Tooltip>
                            </div>
                          </StyledTableCell>
                        </StyledTableRow>
                      );
                    })
                )}
              </TableBody>
            </Table>
          </TableContainer>
        </ThemeProvider>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <TablePagination
            rowsPerPageOptions={[10, 25, 50]}
            component="div"
            count={searchResult.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </div>
      </Paper>
      {openModal && (
        <EditAction
          currID={currID}
          currType={currType}
          currDescription={currDescription}
          currCommands={currCommands}
          handleResetSelected={handleResetSelected}
          handleSuccess={handleSuccess}
        />
      )}

      {openDeleted && (
        <Snackbar
          open={openDeleted}
          autoHideDuration={6000}
          onClose={handleCloseDeleted}
        >
          <Alert onClose={handleCloseDeleted} severity="success">
            You have successfully deleted an Action!
          </Alert>
        </Snackbar>
      )}
      {openSuccess && (
        <Snackbar
          open={openSuccess}
          autoHideDuration={6000}
          onClose={handleCloseSuccess}
        >
          <Alert onClose={handleCloseSuccess} severity="success">
            Action has been saved!
          </Alert>
        </Snackbar>
      )}
      {showDelete && (
        <Dialog
          open={showDelete}
          onClose={handleShowDelete}
          TransitionComponent={ZoomTransition}
        >
          <DialogTitle id="Confirm message">
            {'Are you sure to delete "' + currDescription + '"?'}
          </DialogTitle>
          <DialogContent>
            <DialogContentText id="confirm description">
              You will not be able to recover this action.
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleShowDelete} color="primary">
              Cancel
            </Button>
            <ThemeProvider theme={ButtonTheme}>
              <Button
                onClick={handleConfirmDelete}
                color="secondary"
                autoFocus
                variant="contained"
              >
                Yes, delete it
              </Button>
            </ThemeProvider>
          </DialogActions>
        </Dialog>
      )}
    </div>
  );
};

function ActionsTable() {
  return (
    <React.Fragment>
      <Grid container spacing={6}>
        <Grid item xs={12}>
          <EnhancedTable />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}

export default ActionsTable;
