import React, { useState, useEffect } from "react";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/styles";
import InputLabel from "@material-ui/core/InputLabel";
import NativeSelect from "@material-ui/core/NativeSelect";
import TextField from "@material-ui/core/TextField";
import Snackbar from "@material-ui/core/Snackbar";
import { actionTypes } from "./types";
import SlideTransition from "../../../components/Material/slide";
import ZoomTransition from "../../../components/Material/zoom";

import Alert from "../../../components/Material/alert";
import qs from "qs";
import {
  requestRerender,
  editAction,
  openModalAction,
} from "../../../redux/reducers/actionsPage/actions";
import { Data } from "./types";

import { SAVE_ACTION } from "../../../apis";

import { RootState } from "../../../redux/reducers";
import { useSelector, useDispatch } from "react-redux";
const selector = (state: RootState) => {
  return state.actionsPage;
};

const actionInstructions = {
  title: "You can use the following special variables in the action:",
  lines: [
    { command: "$infile", desc: " - input file" },
    { command: "$outfile", desc: " - output file" },
    { command: "$inputfilename", desc: " - input file name without path" },
    { command: "$outputfilename", desc: " - output file name without path" },
    { command: "$statusurl", desc: " - Video Robot Job Status URL" },
    { command: "$outdir", desc: " - path for output file" },
    { command: "$sambaoutdir", desc: " - samba path for output file" },
    {
      command: "$phphome",
      desc: " - PHP home (path for php executable is $phphome/bin/php)",
    },
    { command: "$scriptdir", desc: " - where the scripts live" },
    { command: "$externalid", desc: " - your job's external id" },
    { command: "$jobid", desc: " - video robot's internal job id" },
    { command: "$stepid", desc: " - video robot's job step number" },
    { command: "$wmvhost", desc: " - WMV external host name" },
    { command: "$wmvuser", desc: " - WMV external user name" },
    { command: "$wmvpass", desc: " - WMV external password" },
  ],
};
const styles = (theme: Theme) =>
  createStyles({
    root: {
      //   margin: 0,
      //   padding: theme.spacing(2),
    },
    closeButton: {
      position: "absolute",
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {},
    contents: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(4),
      color: theme.palette.text.secondary,
    },
    labelWidth: {
      minWidth: 510,
    },
    actionInstructions: {
      display: "flex",
      flexDirection: "row",
    },
    commands: {
      fontWeight: "bold",
      paddingLeft: "10px",
    },
  })
);

type Props = {
  currID?: string;
  currType: actionTypes;
  currDescription: string;
  currCommands: string;
  handleResetSelected: () => void;
  handleSuccess: () => void;
};

export default function EditAction(props: Props) {
  const dispatch = useDispatch();
  const { openModal } = useSelector(selector);

  const classes = useStyles();
  const [id, setID] = useState("");
  const [select, setSelect] = useState<actionTypes>("Compress");
  const [description, setDescription] = useState("");
  const [commands, setCommands] = useState("");
  const [openError, setOpenError] = useState(false);
  const [openHint, setOpenHint] = useState(false);

  const {
    currID,
    currType,
    currDescription,
    currCommands,
    handleResetSelected,
    handleSuccess,
  } = props;

  useEffect(() => {
    if (currDescription.length !== 0) {
      setSelect(currType);
      setDescription(currDescription);
      setCommands(currCommands);
      if (currID) setID(currID);
    }
  }, [currType, currDescription, currCommands, currID]);

  const handleClose = (
    event?: any,
    reason?: "backdropClick" | "escapeKeyDown"
  ) => {
    if (reason === "backdropClick") {
      setOpenHint(true);
      return;
    }
    handleReset();
    handleResetSelected();
    dispatch(openModalAction());
  };
  const handleSave = () => {
    if (description.length === 0 || commands.length === 0) {
      setOpenError(true);
      return;
    }
    addOrEditAction();
    handleReset();
    handleResetSelected();
    handleSuccess();
    dispatch(openModalAction());
  };

  const addOrEditAction = () => {
    const type: string = select;
    console.log(type);

    const action = id.length === 0 ? "add" : "edit";
    let data;
    if (action === "add") {
      data = {
        actionType: type,
        actionDesc: description,
        actionCommand: commands,
      };
    } else {
      data = {
        id: id,
        actionType: type,
        actionDesc: description,
        actionCommand: commands,
      };
    }

    fetch(SAVE_ACTION, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
      },
      body: qs.stringify(data),
    });
    if (action === "edit") {
      let modifiedData: Data = {
        id: parseInt(id),
        category: select,
        description: description,
        action: commands,
      };
      dispatch(editAction(modifiedData));
    } else {
      dispatch(requestRerender());
    }
  };

  const handleSelect = (event: any) => {
    const selectValue: actionTypes = event.target.value;
    setSelect(selectValue);
  };

  const handleDescription = (event: any) => {
    const value = event.target.value;
    setDescription(value);
  };

  const handleCommands = (event: any) => {
    const value = event.target.value;
    setCommands(value);
  };

  const handleReset = () => {
    setSelect("Compress");
    setDescription("");
    setCommands("");
  };
  const handleCloseError = () => {
    setOpenError(false);
  };

  const handleCloseHint = () => {
    setOpenHint(false);
  };

  return (
    <>
      {openModal && (
        <div>
          <Dialog
            onClose={handleClose}
            aria-labelledby="add-action"
            open={openModal}
            maxWidth="md"
            TransitionComponent={ZoomTransition}
            // keepMounted
          >
            <DialogTitle id="add-action" onClose={handleClose}>
              Add Action
            </DialogTitle>
            <DialogContent dividers>
              <div className={classes.contents}>
                <Grid container spacing={3} component={"div"}>
                  <Grid item xs={7}>
                    <Paper className={classes.paper}>
                      <InputLabel shrink htmlFor="age-native-label-placeholder">
                        Type
                      </InputLabel>
                      <NativeSelect
                        className={classes.labelWidth}
                        value={select}
                        onChange={handleSelect}
                      >
                        <option value="Compress">Compress</option>
                        <option value="Carbon Compress">Carbon Compress</option>
                        <option value="WMV">WMV</option>
                        <option value="Thumbnail">Thumbnail</option>
                        <option value="Job Manifest">Job Manifest</option>
                        <option value="Upload">Upload</option>
                        <option value="Notify">Notify</option>
                        <option value="HTTP">HTTP</option>
                        <option value="File Manip">File Manip</option>
                        <option value="thePlatform">thePlatform</option>
                      </NativeSelect>
                    </Paper>
                    <Paper className={classes.paper}>
                      <TextField
                        label="Description"
                        required
                        multiline
                        rows={2}
                        variant="outlined"
                        value={description}
                        className={classes.labelWidth}
                        onChange={handleDescription}
                        inputProps={{ spellCheck: "false" }}
                      />
                    </Paper>
                    <Paper className={classes.paper}>
                      <TextField
                        // id="outlined-multiline-static"
                        label="Commands"
                        multiline
                        rows={10}
                        required
                        value={commands}
                        variant="outlined"
                        className={classes.labelWidth}
                        onChange={handleCommands}
                        inputProps={{ spellCheck: "false" }}
                      />
                    </Paper>
                  </Grid>
                  <Grid item xs={5}>
                    <Paper className={classes.paper}>
                      <Typography variant="subtitle1" color="primary">
                        {actionInstructions.title}
                      </Typography>
                      {actionInstructions.lines.map(
                        (el: any, index: number) => (
                          <div
                            className={classes.actionInstructions}
                            key={index}
                          >
                            <Typography className={classes.commands}>
                              {el.command}
                            </Typography>
                            <Typography style={{ paddingLeft: "5px" }}>
                              {el.desc}
                            </Typography>
                          </div>
                        )
                      )}
                    </Paper>
                  </Grid>
                </Grid>
              </div>
            </DialogContent>
            <DialogActions>
              <Grid container justify="space-between">
                <Grid item>
                  <Button autoFocus onClick={handleReset} color="primary">
                    Reset
                  </Button>
                </Grid>
                <Grid item>
                  <Button autoFocus onClick={handleSave} color="primary">
                    Save
                  </Button>
                </Grid>
              </Grid>
            </DialogActions>
          </Dialog>
          {openError && (
            <Snackbar
              open={openError}
              autoHideDuration={6000}
              onClose={handleCloseError}
            >
              <Alert onClose={handleCloseError} severity="error">
                {description.length === 0
                  ? "Description cannot be blank!"
                  : "Commands cannot be blank!"}
              </Alert>
            </Snackbar>
          )}
          {openHint && (
            <Snackbar
              open={openHint}
              autoHideDuration={5000}
              onClose={handleCloseHint}
            >
              <Alert onClose={handleCloseHint} severity="info">
                Please use the "X" button to close this dialog!
              </Alert>
            </Snackbar>
          )}
        </div>
      )}
    </>
  );
}
